.cover {
    display: block;
    position: relative;
    padding: 10px;
    height: 100%;

    height: 300px;
    width: 200px;

    margin: 0px;
}

.text {
    position: relative;
    z-index: 1;

    left: 0;
    right: 0;
    top: 85%;
    color: white;
    background-color: #222222;
    text-align: center;

    padding: 0px;
    margin: 0px;
}

.text p {
    padding: 0px;
    margin: 0px;
}

.image {
    position: absolute;
    top: 0px;
    height: 100%;
    bottom: 0px;
    width: 100%;
    left: 0px;
    -moz-user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
